import * as THREE from 'three';
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js';
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js';
import { UnrealBloomPass } from 'three/examples/jsm/postprocessing/UnrealBloomPass.js';
import { ShaderPass } from 'three/examples/jsm/postprocessing/ShaderPass.js';
import { FXAAShader } from 'three/examples/jsm/shaders/FXAAShader.js';

// Class to handle Assassin's Creed visual style and atmosphere
export class VisualEffects {
    constructor(scene, camera, renderer) {
        this.scene = scene;
        this.camera = camera;
        this.renderer = renderer;
        
        // Post-processing
        this.composer = null;
        this.bloomPass = null;
        
        // Eagle vision mode
        this.eagleVisionActive = false;
        this.originalFog = null;
        this.originalBackground = null;
        
        // Ambient sounds
        this.audioListener = new THREE.AudioListener();
        this.ambientSound = new THREE.Audio(this.audioListener);
        this.eagleVisionSound = new THREE.Audio(this.audioListener);
        
        // Particle systems
        this.dustParticles = null;
        this.memoryFragments = null;
        
        // Synchronization points
        this.syncPoints = [];
        
        // Initialize effects
        this.initPostProcessing();
        this.createParticleSystems();
    }
    
    initPostProcessing() {
        // Create effect composer
        this.composer = new EffectComposer(this.renderer);
        
        // Add render pass
        const renderPass = new RenderPass(this.scene, this.camera);
        this.composer.addPass(renderPass);
        
        // Add bloom pass for golden glow
        this.bloomPass = new UnrealBloomPass(
            new THREE.Vector2(window.innerWidth, window.innerHeight),
            0.5,    // strength
            0.4,    // radius
            0.85    // threshold
        );
        this.composer.addPass(this.bloomPass);
        
        // Add FXAA pass for anti-aliasing
        const fxaaPass = new ShaderPass(FXAAShader);
        fxaaPass.material.uniforms['resolution'].value.set(
            1 / window.innerWidth, 
            1 / window.innerHeight
        );
        this.composer.addPass(fxaaPass);
        
        // Store original scene properties
        this.originalFog = this.scene.fog ? this.scene.fog.clone() : null;
        this.originalBackground = this.scene.background ? this.scene.background.clone() : null;
    }
    
    createParticleSystems() {
        // Create dust particles
        const dustGeometry = new THREE.BufferGeometry();
        const dustCount = 1000;
        const dustPositions = new Float32Array(dustCount * 3);
        const dustSizes = new Float32Array(dustCount);
        
        for (let i = 0; i < dustCount; i++) {
            const i3 = i * 3;
            dustPositions[i3] = (Math.random() - 0.5) * 100;
            dustPositions[i3 + 1] = Math.random() * 30;
            dustPositions[i3 + 2] = (Math.random() - 0.5) * 100;
            dustSizes[i] = Math.random() * 2;
        }
        
        dustGeometry.setAttribute('position', new THREE.BufferAttribute(dustPositions, 3));
        dustGeometry.setAttribute('size', new THREE.BufferAttribute(dustSizes, 1));
        
        const dustMaterial = new THREE.PointsMaterial({
            color: 0xCCCCCC,
            size: 0.1,
            transparent: true,
            opacity: 0.2,
            map: new THREE.TextureLoader().load('assets/textures/dust_particle.png'),
            blending: THREE.AdditiveBlending,
            depthWrite: false
        });
        
        this.dustParticles = new THREE.Points(dustGeometry, dustMaterial);
        this.scene.add(this.dustParticles);
        
        // Create memory fragments (only visible in eagle vision)
        const fragmentGeometry = new THREE.BufferGeometry();
        const fragmentCount = 200;
        const fragmentPositions = new Float32Array(fragmentCount * 3);
        const fragmentSizes = new Float32Array(fragmentCount);
        const fragmentColors = new Float32Array(fragmentCount * 3);
        
        for (let i = 0; i < fragmentCount; i++) {
            const i3 = i * 3;
            fragmentPositions[i3] = (Math.random() - 0.5) * 80;
            fragmentPositions[i3 + 1] = Math.random() * 20 + 1;
            fragmentPositions[i3 + 2] = (Math.random() - 0.5) * 80;
            fragmentSizes[i] = Math.random() * 3 + 1;
            
            // Blue-cyan color for memory fragments
            fragmentColors[i3] = 0.1;
            fragmentColors[i3 + 1] = 0.7 + Math.random() * 0.3;
            fragmentColors[i3 + 2] = 1.0;
        }
        
        fragmentGeometry.setAttribute('position', new THREE.BufferAttribute(fragmentPositions, 3));
        fragmentGeometry.setAttribute('size', new THREE.BufferAttribute(fragmentSizes, 1));
        fragmentGeometry.setAttribute('color', new THREE.BufferAttribute(fragmentColors, 3));
        
        const fragmentMaterial = new THREE.PointsMaterial({
            size: 2,
            transparent: true,
            opacity: 0,
            map: new THREE.TextureLoader().load('assets/textures/memory_fragment.png'),
            blending: THREE.AdditiveBlending,
            depthWrite: false,
            vertexColors: true
        });
        
        this.memoryFragments = new THREE.Points(fragmentGeometry, fragmentMaterial);
        this.scene.add(this.memoryFragments);
    }
    
    loadSounds(audioLoader) {
        // Load ambient sound
        audioLoader.load('assets/sounds/ambient_mansion.mp3', (buffer) => {
            this.ambientSound.setBuffer(buffer);
            this.ambientSound.setLoop(true);
            this.ambientSound.setVolume(0.3);
            this.ambientSound.play();
        });
        
        // Load eagle vision sound
        audioLoader.load('assets/sounds/eagle_vision.mp3', (buffer) => {
            this.eagleVisionSound.setBuffer(buffer);
            this.eagleVisionSound.setLoop(true);
            this.eagleVisionSound.setVolume(0.5);
        });
    }
    
    createSynchronizationPoints() {
        // Create synchronization points at key locations
        const syncPointLocations = [
            { position: new THREE.Vector3(0, 35, 0), name: "Tax Tower Observatory" },
            { position: new THREE.Vector3(-25, 15, -20), name: "Fixed Income Wing" },
            { position: new THREE.Vector3(0, 15, -35), name: "Equities Wing" },
            { position: new THREE.Vector3(25, 15, -20), name: "Pooled Investments Wing" }
        ];
        
        syncPointLocations.forEach(point => {
            // Create a visual marker for the sync point
            const syncGeometry = new THREE.SphereGeometry(1, 16, 16);
            const syncMaterial = new THREE.MeshBasicMaterial({
                color: 0x00FFFF,
                transparent: true,
                opacity: 0.7,
                wireframe: true
            });
            
            const syncPoint = new THREE.Mesh(syncGeometry, syncMaterial);
            syncPoint.position.copy(point.position);
            syncPoint.userData.name = point.name;
            syncPoint.userData.isSyncPoint = true;
            
            // Add rotating outer sphere
            const outerGeometry = new THREE.SphereGeometry(1.5, 16, 16);
            const outerMaterial = new THREE.MeshBasicMaterial({
                color: 0x00FFFF,
                transparent: true,
                opacity: 0.3,
                wireframe: true
            });
            
            const outerSphere = new THREE.Mesh(outerGeometry, outerMaterial);
            syncPoint.add(outerSphere);
            
            // Add to scene and store reference
            this.scene.add(syncPoint);
            this.syncPoints.push(syncPoint);
        });
    }
    
    toggleEagleVision() {
        this.eagleVisionActive = !this.eagleVisionActive;
        
        if (this.eagleVisionActive) {
            // Apply eagle vision effect
            
            // 1. Change scene fog and background
            this.scene.fog = new THREE.FogExp2(0x000033, 0.015);
            this.scene.background = new THREE.Color(0x000033);
            
            // 2. Adjust bloom effect
            this.bloomPass.strength = 1.5;
            this.bloomPass.radius = 0.7;
            this.bloomPass.threshold = 0.3;
            
            // 3. Show memory fragments
            this.memoryFragments.material.opacity = 0.8;
            
            // 4. Play eagle vision sound
            if (this.eagleVisionSound.buffer && !this.eagleVisionSound.isPlaying) {
                this.eagleVisionSound.play();
            }
            
            // 5. Reduce ambient sound
            if (this.ambientSound.isPlaying) {
                this.ambientSound.setVolume(0.1);
            }
            
            // 6. Make sync points more visible
            this.syncPoints.forEach(point => {
                point.material.opacity = 1.0;
                point.children[0].material.opacity = 0.7;
            });
            
        } else {
            // Restore normal vision
            
            // 1. Restore original fog and background
            this.scene.fog = this.originalFog ? this.originalFog.clone() : null;
            this.scene.background = this.originalBackground ? this.originalBackground.clone() : new THREE.Color(0x111111);
            
            // 2. Reset bloom effect
            this.bloomPass.strength = 0.5;
            this.bloomPass.radius = 0.4;
            this.bloomPass.threshold = 0.85;
            
            // 3. Hide memory fragments
            this.memoryFragments.material.opacity = 0;
            
            // 4. Stop eagle vision sound
            if (this.eagleVisionSound.isPlaying) {
                this.eagleVisionSound.stop();
            }
            
            // 5. Restore ambient sound
            if (this.ambientSound.isPlaying) {
                this.ambientSound.setVolume(0.3);
            }
            
            // 6. Make sync points less visible
            this.syncPoints.forEach(point => {
                point.material.opacity = 0.7;
                point.children[0].material.opacity = 0.3;
            });
        }
        
        return this.eagleVisionActive;
    }
    
    synchronize(syncPoint, camera, callback) {
        // Start synchronization animation
        const startPosition = camera.position.clone();
        const targetPosition = syncPoint.position.clone().add(new THREE.Vector3(0, 2, 0));
        
        // Store original camera rotation
        const startQuaternion = camera.quaternion.clone();
        
        // Calculate target rotation (looking slightly down)
        const targetQuaternion = new THREE.Quaternion();
        const lookAtVector = new THREE.Vector3(0, -1, 0);
        camera.lookAt(lookAtVector);
        targetQuaternion.copy(camera.quaternion);
        
        // Restore original rotation for animation
        camera.quaternion.copy(startQuaternion);
        
        // Animation parameters
        const duration = 3000; // ms
        const startTime = Date.now();
        
        // Create animation function
        const animate = () => {
            const elapsedTime = Date.now() - startTime;
            const progress = Math.min(elapsedTime / duration, 1);
            
            // Ease in-out function
            const easeProgress = progress < 0.5 
                ? 2 * progress * progress 
                : -1 + (4 - 2 * progress) * progress;
            
            // Move camera
            camera.position.lerpVectors(startPosition, targetPosition, easeProgress);
            
            // Rotate camera
            camera.quaternion.slerpQuaternions(startQuaternion, targetQuaternion, easeProgress);
            
            // Continue animation if not complete
            if (progress < 1) {
                requestAnimationFrame(animate);
            } else {
                // Animation complete, trigger callback
                if (callback) callback(syncPoint.userData.name);
            }
        };
        
        // Start animation
        animate();
    }
    
    createAssassinEffects() {
        // Create hay bales (stylized as bond bundles) for leaps of faith
        const hayBaleLocations = [
            new THREE.Vector3(-25, 0, -25),
            new THREE.Vector3(0, 0, -40),
            new THREE.Vector3(25, 0, -25)
        ];
        
        hayBaleLocations.forEach(location => {
            const hayGroup = new THREE.Group();
            hayGroup.position.copy(location);
            
            // Create base
            const baseGeometry = new THREE.BoxGeometry(3, 1, 3);
            const baseMaterial = new THREE.MeshStandardMaterial({
                color: 0xD4AF37,
                roughness: 0.8,
                metalness: 0.2
            });
            
            const base = new THREE.Mesh(baseGeometry, baseMaterial);
            hayGroup.add(base);
            
            // Create bond papers sticking out
            for (let i = 0; i < 20; i++) {
                const paperGeometry = new THREE.BoxGeometry(0.5, 0.05, 0.7);
                const paperMaterial = new THREE.MeshStandardMaterial({
                    color: 0xFFF8E7,
                    roughness: 0.9,
                    metalness: 0.1
                });
                
                const paper = new THREE.Mesh(paperGeometry, paperMaterial);
                
                // Random position and rotation
                paper.position.set(
                    (Math.random() - 0.5) * 2,
                    0.5 + Math.random() * 0.5,
                    (Math.random() - 0.5) * 2
                );
                
                paper.rotation.set(
                    Math.random() * Math.PI,
                    Math.random() * Math.PI,
                    Math.random() * Math.PI
                );
                
                hayGroup.add(paper);
            }
            
            // Add to scene
            this.scene.add(hayGroup);
        });
        
        // Create hidden mechanisms
        this.createHiddenMechanisms();
        
        // Create ancient codex pages
        this.createCodexPages();
    }
    
    createHiddenMechanisms() {
        // Create hidden buttons that reveal secret information
        const hiddenButtonLocations = [
            { position: new THREE.Vector3(-22, 1.5, -18), info: "Secret Bond Rating System" },
            { position: new THREE.Vector3(2, 1.5, -30), info: "Hidden Stock Market Patterns" },
            { position: new THREE.Vector3(22, 1.5, -18), info: "Mutual Fund Fee Structures" }
        ];
        
        hiddenButtonLocations.forEach(data => {
            // Create a subtle eagle symbol on the wall
            const eagleGeometry = new THREE.CircleGeometry(0.2, 32);
            const eagleMaterial = new THREE.MeshStandardMaterial({
                color: 0xD4AF37,
                roughness: 0.3,
                metalness: 0.8,
                transparent: true,
                opacity: 0.3
            });
            
            const eagle = new THREE.Mesh(eagleGeometry, eagleMaterial);
            eagle.position.copy(data.position);
            
            // Rotate to face outward from wall
            if (data.position.x < -10) {
                eagle.rotation.y = Math.PI / 2; // Left wing
            } else if (data.position.x > 10) {
                eagle.rotation.y = -Math.PI / 2; // Right wing
            } else {
                eagle.rotation.y = 0; // Center
            }
            
            // Add user data for interaction
            eagle.userData.isHiddenMechanism = true;
            eagle.userData.info = data.info;
            
            this.scene.add(eagle);
        });
    }
    
    createCodexPages() {
        // Create ancient-looking codex pages with investment wisdom
        const codexLocations = [
            { position: new THREE.Vector3(-20, 1, -15), title: "Bonds: The Foundation of Wealth", content: "The wise investor builds upon the stable ground of fixed income..." },
            { position: new THREE.Vector3(0, 1, -25), title: "Equities: The Growth of Nations", content: "As kingdoms rise through commerce, so too shall your wealth through ownership..." },
            { position: new THREE.Vector3(20, 1, -15), title: "Diversification: The Shield of Portfolios", content: "The master assassin never relies on a single weapon, nor should the investor..." }
        ];
        
        codexLocations.forEach(data => {
            const codexGroup = new THREE.Group();
            codexGroup.position.copy(data.position);
            
            // Create page
            const pageGeometry = new THREE.PlaneGeometry(1, 1.4);
            const pageTexture = new THREE.TextureLoader().load('assets/textures/codex_page.png');
            const pageMaterial = new THREE.MeshStandardMaterial({
                map: pageTexture,
                roughness: 0.9,
                metalness: 0.1
            });
            
            const page = new THREE.Mesh(pageGeometry, pageMaterial);
            
            // Rotate to lay flat
            page.rotation.x = -Math.PI / 2;
            
            // Add subtle glow in eagle vision
            const glowGeometry = new THREE.PlaneGeometry(1.2, 1.6);
            const glowMaterial = new THREE.MeshBasicMaterial({
                color: 0x00FFFF,
                transparent: true,
                opacity: 0,
                side: THREE.DoubleSide
            });
            
            const glow = new THREE.Mesh(glowGeometry, glowMaterial);
            glow.rotation.x = -Math.PI / 2;
            glow.position.y = -0.01;
            
            // Add user data for interaction
            page.userData.isCodexPage = true;
            page.userData.title = data.title;
            page.userData.content = data.content;
            page.userData.glow = glow;
            
            codexGroup.add(page);
            codexGroup.add(glow);
            
            this.scene.add(codexGroup);
        });
    }
    
    update(delta, camera) {
        // Animate dust particles
        if (this.dustParticles) {
            const positions = this.dustParticles.geometry.attributes.position.array;
            const count = positions.length / 3;
            
            for (let i = 0; i < count; i++) {
                const i3 = i * 3;
                
                // Slow vertical movement
                positions[i3 + 1] -= delta * 0.1;
                
                // Reset particles that go below ground
                if (positions[i3 + 1] < 0) {
                    positions[i3 + 1] = 30;
                }
            }
            
            this.dustParticles.geometry.attributes.position.needsUpdate = true;
        }
        
        // Animate memory fragments in eagle vision
        if (this.memoryFragments && this.eagleVisionActive) {
            const positions = this.memoryFragments.geometry.attributes.position.array;
            const count = positions.length / 3;
            
            for (let i = 0; i < count; i++) {
                const i3 = i * 3;
                
                // Gentle floating motion
                positions[i3] += Math.sin(Date.now() * 0.001 + i) * delta * 0.1;
                positions[i3 + 1] += Math.cos(Date.now() * 0.001 + i) * delta * 0.05;
                positions[i3 + 2] += Math.sin(Date.now() * 0.0005 + i) * delta * 0.1;
            }
            
            this.memoryFragments.geometry.attributes.position.needsUpdate = true;
        }
        
        // Animate synchronization points
        this.syncPoints.forEach(point => {
            if (point.children.length > 0) {
                point.children[0].rotation.y += delta * 0.5;
                point.children[0].rotation.x += delta * 0.3;
            }
        });
        
        // Update codex page glows in eagle vision
        this.scene.traverse(object => {
            if (object.userData && object.userData.isCodexPage) {
                if (object.userData.glow) {
                    if (this.eagleVisionActive) {
                        object.userData.glow.material.opacity = 0.5 + Math.sin(Date.now() * 0.002) * 0.2;
                    } else {
                        object.userData.glow.material.opacity = 0;
                    }
                }
            }
        });
        
        // Update hidden mechanisms in eagle vision
        this.scene.traverse(object => {
            if (object.userData && object.userData.isHiddenMechanism) {
                if (this.eagleVisionActive) {
                    object.material.opacity = 0.7 + Math.sin(Date.now() * 0.003) * 0.3;
                } else {
                    object.material.opacity = 0.3;
                }
            }
        });
        
        // Render with post-processing
        if (this.composer) {
            this.composer.render();
        }
    }
    
    resize() {
        if (this.composer) {
            this.composer.setSize(window.innerWidth, window.innerHeight);
        }
        
        // Update FXAA pass
        const fxaaPass = this.composer.passes.find(pass => pass.material && pass.material.uniforms.resolution);
        if (fxaaPass) {
            fxaaPass.material.uniforms.resolution.value.set(
                1 / window.innerWidth, 
                1 / window.innerHeight
            );
        }
    }
    
    getAudioListener() {
        return this.audioListener;
    }
}
