import * as THREE from 'three';
import { PointerLockControls } from 'three/examples/jsm/controls/PointerLockControls.js';

// Class to handle player movement and navigation
export class PlayerController {
    constructor(camera, domElement) {
        this.camera = camera;
        this.domElement = domElement;
        
        // Set up pointer lock controls
        this.controls = new PointerLockControls(this.camera, this.domElement);
        
        // Movement state
        this.moveForward = false;
        this.moveBackward = false;
        this.moveLeft = false;
        this.moveRight = false;
        this.moveUp = false; // For flying mode
        this.moveDown = false; // For flying mode
        this.sprint = false;
        
        // Movement parameters
        this.velocity = new THREE.Vector3();
        this.direction = new THREE.Vector3();
        this.walkSpeed = 10.0;
        this.sprintSpeed = 20.0;
        this.flyMode = false;
        
        // Collision detection
        this.colliders = [];
        this.playerHeight = 1.7;
        this.playerRadius = 0.5;
        
        // Current location tracking
        this.currentLocation = 'Foyer';
        this.locationChangeCallback = null;
        this.locationTriggers = [];
        
        // Setup event listeners
        this.setupEventListeners();
    }
    
    setupEventListeners() {
        document.addEventListener('keydown', this.onKeyDown.bind(this));
        document.addEventListener('keyup', this.onKeyUp.bind(this));
        
        // Lock pointer on click
        this.domElement.addEventListener('click', () => {
            if (!this.controls.isLocked) {
                this.controls.lock();
            }
        });
        
        // Handle pointer lock changes
        document.addEventListener('pointerlockchange', this.onPointerLockChange.bind(this));
    }
    
    onPointerLockChange() {
        if (document.pointerLockElement === this.domElement) {
            // Pointer is locked, enable controls
            this.controls.enabled = true;
        } else {
            // Pointer is unlocked, disable controls
            this.controls.enabled = false;
        }
    }
    
    onKeyDown(event) {
        if (!this.controls.isLocked) return;
        
        switch (event.code) {
            case 'KeyW':
            case 'ArrowUp':
                this.moveForward = true;
                break;
            case 'KeyS':
            case 'ArrowDown':
                this.moveBackward = true;
                break;
            case 'KeyA':
            case 'ArrowLeft':
                this.moveLeft = true;
                break;
            case 'KeyD':
            case 'ArrowRight':
                this.moveRight = true;
                break;
            case 'Space':
                if (this.flyMode) {
                    this.moveUp = true;
                }
                break;
            case 'ShiftLeft':
            case 'ShiftRight':
                this.sprint = true;
                break;
            case 'ControlLeft':
            case 'ControlRight':
                if (this.flyMode) {
                    this.moveDown = true;
                }
                break;
            case 'KeyF':
                // Toggle fly mode
                this.flyMode = !this.flyMode;
                break;
        }
    }
    
    onKeyUp(event) {
        if (!this.controls.isLocked) return;
        
        switch (event.code) {
            case 'KeyW':
            case 'ArrowUp':
                this.moveForward = false;
                break;
            case 'KeyS':
            case 'ArrowDown':
                this.moveBackward = false;
                break;
            case 'KeyA':
            case 'ArrowLeft':
                this.moveLeft = false;
                break;
            case 'KeyD':
            case 'ArrowRight':
                this.moveRight = false;
                break;
            case 'Space':
                this.moveUp = false;
                break;
            case 'ShiftLeft':
            case 'ShiftRight':
                this.sprint = false;
                break;
            case 'ControlLeft':
            case 'ControlRight':
                this.moveDown = false;
                break;
        }
    }
    
    setColliders(colliders) {
        this.colliders = colliders;
    }
    
    setLocationTriggers(triggers) {
        this.locationTriggers = triggers;
    }
    
    setLocationChangeCallback(callback) {
        this.locationChangeCallback = callback;
    }
    
    checkLocationTriggers() {
        const position = this.camera.position.clone();
        
        for (const trigger of this.locationTriggers) {
            if (position.distanceTo(trigger.position) < trigger.radius) {
                if (this.currentLocation !== trigger.name) {
                    this.currentLocation = trigger.name;
                    if (this.locationChangeCallback) {
                        this.locationChangeCallback(this.currentLocation);
                    }
                }
                return;
            }
        }
    }
    
    checkCollisions() {
        const position = this.camera.position.clone();
        position.y -= this.playerHeight / 2; // Adjust to player center
        
        for (const collider of this.colliders) {
            if (collider.geometry.type === 'BoxGeometry') {
                // Box collision
                const box = new THREE.Box3().setFromObject(collider);
                const sphere = new THREE.Sphere(position, this.playerRadius);
                
                if (box.intersectsSphere(sphere)) {
                    // Calculate push-back direction
                    const boxCenter = new THREE.Vector3();
                    box.getCenter(boxCenter);
                    
                    const pushDirection = position.clone().sub(boxCenter).normalize();
                    const pushDistance = this.playerRadius - position.distanceTo(boxCenter);
                    
                    // Push player away from collision
                    this.camera.position.add(pushDirection.multiplyScalar(pushDistance));
                }
            } else if (collider.geometry.type === 'SphereGeometry') {
                // Sphere collision
                const colliderPosition = collider.position.clone();
                const colliderRadius = collider.geometry.parameters.radius;
                
                const distance = position.distanceTo(colliderPosition);
                const minDistance = this.playerRadius + colliderRadius;
                
                if (distance < minDistance) {
                    // Calculate push-back direction
                    const pushDirection = position.clone().sub(colliderPosition).normalize();
                    const pushDistance = minDistance - distance;
                    
                    // Push player away from collision
                    this.camera.position.add(pushDirection.multiplyScalar(pushDistance));
                }
            }
        }
    }
    
    update(delta) {
        if (!this.controls.isLocked) return;
        
        // Apply friction to slow down movement
        this.velocity.x -= this.velocity.x * 10.0 * delta;
        this.velocity.z -= this.velocity.z * 10.0 * delta;
        
        if (this.flyMode) {
            this.velocity.y -= this.velocity.y * 10.0 * delta;
        } else {
            // Apply gravity in non-fly mode
            this.velocity.y -= 9.8 * 100.0 * delta; // 100.0 = mass
        }
        
        // Calculate movement direction
        this.direction.z = Number(this.moveForward) - Number(this.moveBackward);
        this.direction.x = Number(this.moveRight) - Number(this.moveLeft);
        this.direction.normalize(); // Normalize for consistent movement in all directions
        
        // Apply movement speed
        const speed = this.sprint ? this.sprintSpeed : this.walkSpeed;
        
        if (this.moveForward || this.moveBackward) {
            this.velocity.z -= this.direction.z * speed * delta;
        }
        
        if (this.moveLeft || this.moveRight) {
            this.velocity.x -= this.direction.x * speed * delta;
        }
        
        if (this.flyMode) {
            if (this.moveUp) {
                this.velocity.y += speed * delta;
            }
            if (this.moveDown) {
                this.velocity.y -= speed * delta;
            }
        }
        
        // Move the camera
        this.controls.moveRight(-this.velocity.x * delta);
        this.controls.moveForward(-this.velocity.z * delta);
        
        // Apply vertical movement in fly mode or gravity in non-fly mode
        this.camera.position.y += this.velocity.y * delta;
        
        // Prevent falling below ground level in non-fly mode
        if (!this.flyMode && this.camera.position.y < this.playerHeight) {
            this.camera.position.y = this.playerHeight;
            this.velocity.y = 0;
        }
        
        // Check for collisions
        this.checkCollisions();
        
        // Check for location changes
        this.checkLocationTriggers();
    }
    
    teleportTo(position, lookAt = null) {
        this.camera.position.copy(position);
        
        if (lookAt) {
            this.camera.lookAt(lookAt);
            this.controls.update();
        }
    }
    
    getCurrentLocation() {
        return this.currentLocation;
    }
    
    lock() {
        this.controls.lock();
    }
    
    unlock() {
        this.controls.unlock();
    }
    
    isLocked() {
        return this.controls.isLocked;
    }
}
