        // Sundial
        const sundialObject = new THREE.Group();
        sundialObject.position.set(25, 6, -18);
        
        const dialBaseGeometry = new THREE.CylinderGeometry(0.5, 0.5, 0.1, 32);
        const dialBase = new THREE.Mesh(dialBaseGeometry, this.materials.stone);
        sundialObject.add(dialBase);
        
        const gnomenGeometry = new THREE.ConeGeometry(0.05, 0.5, 8);
        const gnomen = new THREE.Mesh(gnomenGeometry, this.materials.bronze);
        gnomen.position.y = 0.25;
        gnomen.rotation.x = Math.PI / 2;
        sundialObject.add(gnomen);
        
        // Make interactive
        this.makeObjectInteractive(sundialObject, 
            this.objectData.pooledInvestmentsWing.mutualFundsHall.sundial.name, 
            this.objectData.pooledInvestmentsWing.mutualFundsHall.sundial.description
        );
        
        this.scene.add(sundialObject);
        
        // ETFs Room objects
        
        // Live Stock Ticker
        const tickerObject = new THREE.Group();
        tickerObject.position.set(27, 6, -18);
        
        const screenGeometry = new THREE.BoxGeometry(1.5, 0.5, 0.1);
        const screen = new THREE.Mesh(screenGeometry, this.materials.glass);
        tickerObject.add(screen);
        
        // Make interactive
        this.makeObjectInteractive(tickerObject, 
            this.objectData.pooledInvestmentsWing.etfsRoom.liveStockTicker.name, 
            this.objectData.pooledInvestmentsWing.etfsRoom.liveStockTicker.description
        );
        
        this.scene.add(tickerObject);
        
        // REITs Room objects
        
        // Skyscraper
        const skyscraperObject = new THREE.Group();
        skyscraperObject.position.set(25, 6, -22);
        
