import * as THREE from 'three';
import { MansionStructure } from './components/MansionStructure.js';
import { InvestmentObjects } from './components/InvestmentObjects.js';
import { PlayerController } from './components/PlayerController.js';
import { DialogueSystem } from './components/DialogueSystem.js';
import { VisualEffects } from './components/VisualEffects.js';

// Main application class
class MemoryMansionApp {
    constructor() {
        // DOM elements
        this.container = document.getElementById('scene-container');
        this.loadingScreen = document.getElementById('loading-screen');
        this.loadingBar = document.getElementById('loading-bar');
        this.locationIndicator = document.getElementById('location-indicator');
        
        // Three.js components
        this.scene = new THREE.Scene();
        this.camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
        this.renderer = new THREE.WebGLRenderer({ antialias: true });
        
        // Game systems
        this.clock = new THREE.Clock();
        this.audioLoader = new THREE.AudioLoader();
        this.textureLoader = new THREE.TextureLoader();
        this.mansionStructure = null;
        this.investmentObjects = null;
        this.playerController = null;
        this.dialogueSystem = null;
        this.visualEffects = null;
        
        // Game state
        this.isLoading = true;
        this.eagleVisionActive = false;
        
        // Initialize the application
        this.init();
    }
    
    init() {
        // Setup renderer
        this.renderer.setSize(window.innerWidth, window.innerHeight);
        this.renderer.setPixelRatio(window.devicePixelRatio);
        this.renderer.shadowMap.enabled = true;
        this.renderer.shadowMap.type = THREE.PCFSoftShadowMap;
        this.renderer.outputEncoding = THREE.sRGBEncoding;
        this.container.appendChild(this.renderer.domElement);
        
        // Setup scene
        this.scene.background = new THREE.Color(0x111111);
        this.scene.fog = new THREE.FogExp2(0x111111, 0.02);
        
        // Setup camera
        this.camera.position.set(0, 1.7, 5);
        
        // Initialize systems
        this.initSystems();
        
        // Setup event listeners
        window.addEventListener('resize', this.onWindowResize.bind(this));
        document.addEventListener('keydown', this.onKeyDown.bind(this));
        this.container.addEventListener('click', this.onClick.bind(this));
        document.addEventListener('mousemove', this.onMouseMove.bind(this));
        
        // Start loading assets
        this.loadAssets();
        
        // Start animation loop
        this.animate();
    }
    
    initSystems() {
        // Initialize dialogue system first (needed by other systems)
        this.dialogueSystem = new DialogueSystem();
        
        // Initialize investment objects
        this.investmentObjects = new InvestmentObjects(this.scene, this.dialogueSystem);
        
        // Initialize player controller
        this.playerController = new PlayerController(this.camera, this.renderer.domElement);
        
        // Initialize visual effects
        this.visualEffects = new VisualEffects(this.scene, this.camera, this.renderer);
        
        // Initialize mansion structure (must be last to access all other systems)
        this.mansionStructure = new MansionStructure(this.scene);
    }
    
    loadAssets() {
        // Create asset loading queue
        const totalAssets = 10; // Adjust based on actual asset count
        let loadedAssets = 0;
        
        // Function to update loading progress
        const updateProgress = () => {
            loadedAssets++;
            const progress = (loadedAssets / totalAssets) * 100;
            this.loadingBar.style.width = `${progress}%`;
            
            if (loadedAssets >= totalAssets) {
                this.finishLoading();
            }
        };
        
        // Load audio assets
        this.loadAudioAssets(updateProgress);
        
        // Load texture assets
        this.loadTextureAssets(updateProgress);
        
        // Create mansion structure
        this.mansionStructure.createMansion();
        updateProgress();
        
        // Create interactive objects
        this.investmentObjects.createInteractiveObjects();
        updateProgress();
        
        // Create visual effects
        this.visualEffects.createAssassinEffects();
        this.visualEffects.createSynchronizationPoints();
        updateProgress();
        
        // Setup location triggers
        this.setupLocationTriggers();
        updateProgress();
    }
    
    loadAudioAssets(updateCallback) {
        // Get audio listeners from systems
        const audioListeners = [
            this.dialogueSystem.getAudioListener(),
            this.visualEffects.getAudioListener()
        ];
        
        // Add audio listener to camera
        this.camera.add(audioListeners[0]);
        
        // Load dialogue sounds
        this.dialogueSystem.loadSounds(this.audioLoader);
        updateCallback();
        
        // Load visual effects sounds
        this.visualEffects.loadSounds(this.audioLoader);
        updateCallback();
    }
    
    loadTextureAssets(updateCallback) {
        // Load common textures
        const textures = [
            'marble.jpg',
            'wood.jpg',
            'gold.jpg',
            'paper.jpg',
            'dust_particle.png',
            'memory_fragment.png'
        ];
        
        textures.forEach(texture => {
            this.textureLoader.load(`assets/textures/${texture}`, () => {
                updateCallback();
            });
        });
    }
    
    setupLocationTriggers() {
        // Create location triggers for different areas of the mansion
        const locationTriggers = [
            { position: new THREE.Vector3(0, 0, 0), radius: 15, name: 'Foyer' },
            { position: new THREE.Vector3(-25, 5, -20), radius: 10, name: 'Fixed Income Wing' },
            { position: new THREE.Vector3(0, 5, -35), radius: 10, name: 'Equities Wing' },
            { position: new THREE.Vector3(25, 5, -20), radius: 10, name: 'Pooled Investments Wing' },
            { position: new THREE.Vector3(-15, -10, 15), radius: 10, name: 'Derivatives Basement' },
            { position: new THREE.Vector3(15, 20, 15), radius: 10, name: 'Alternatives Attic' },
            { position: new THREE.Vector3(0, 20, 0), radius: 5, name: 'Tax Tower' }
        ];
        
        // Set location triggers in player controller
        this.playerController.setLocationTriggers(locationTriggers);
        this.playerController.setLocationChangeCallback(this.onLocationChange.bind(this));
    }
    
    finishLoading() {
        // Hide loading screen with fade out
        this.loadingScreen.style.opacity = '0';
        
        setTimeout(() => {
            this.loadingScreen.style.display = 'none';
            this.isLoading = false;
            
            // Show tutorial
            this.showTutorial();
        }, 1000);
    }
    
    showTutorial() {
        // Show tutorial dialogue
        this.dialogueSystem.showTutorial('Welcome to the Investment Vehicle Memory Mansion', [
            'Use WASD or arrow keys to move around the mansion',
            'Use mouse to look around',
            'Click on objects to learn about investment vehicles',
            'Press F to toggle Eagle Vision mode to highlight interactive objects',
            'Visit Synchronization Points to get an overview of each area',
            'Press ESC to close dialogues or exit pointer lock'
        ]);
    }
    
    onWindowResize() {
        // Update camera aspect ratio
        this.camera.aspect = window.innerWidth / window.innerHeight;
        this.camera.updateProjectionMatrix();
        
        // Update renderer size
        this.renderer.setSize(window.innerWidth, window.innerHeight);
        
        // Update visual effects
        this.visualEffects.resize();
    }
    
    onKeyDown(event) {
        // Toggle eagle vision with V key
        if (event.code === 'KeyV' && !this.dialogueSystem.isDialogueVisible()) {
            this.eagleVisionActive = this.visualEffects.toggleEagleVision();
            this.investmentObjects.toggleEagleVision();
        }
    }
    
    onClick(event) {
        // Skip if dialogue is visible
        if (this.dialogueSystem.isDialogueVisible()) {
            return;
        }
        
        // Check for object interaction
        const objectClicked = this.investmentObjects.onClick(event, this.camera);
        
        // If no object was clicked, check for synchronization points
        if (!objectClicked) {
            this.checkSyncPointClick(event);
        }
    }
    
    checkSyncPointClick(event) {
        // Calculate mouse position in normalized device coordinates
        const mouse = new THREE.Vector2();
        mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
        mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;
        
        // Create raycaster
        const raycaster = new THREE.Raycaster();
        raycaster.setFromCamera(mouse, this.camera);
        
        // Find all objects in the scene
        const intersects = raycaster.intersectObjects(this.scene.children, true);
        
        // Check for sync point interaction
        for (const intersect of intersects) {
            const object = intersect.object;
            
            // Check if object or its parent is a sync point
            let syncPoint = null;
            
            if (object.userData && object.userData.isSyncPoint) {
                syncPoint = object;
            } else if (object.parent && object.parent.userData && object.parent.userData.isSyncPoint) {
                syncPoint = object.parent;
            }
            
            if (syncPoint) {
                // Start synchronization
                this.visualEffects.synchronize(syncPoint, this.camera, this.onSynchronizationComplete.bind(this));
                break;
            }
        }
    }
    
    onSynchronizationComplete(locationName) {
        // Show overview of the area
        this.dialogueSystem.showCodexEntry(`${locationName} Overview`, 
            `This area of the Memory Mansion contains investment vehicles related to ${locationName}. Explore the rooms to discover different investment types and their characteristics.`,
            'Memory Mansion Area'
        );
    }
    
    onMouseMove(event) {
        // Update investment objects for highlighting
        this.investmentObjects.onMouseMove(event);
    }
    
    onLocationChange(newLocation) {
        // Update location indicator
        this.locationIndicator.textContent = `Location: ${newLocation}`;
        
        // Fade in the text with animation
        this.locationIndicator.style.opacity = '0';
        this.locationIndicator.style.transform = 'translateY(-10px)';
        
        setTimeout(() => {
            this.locationIndicator.style.opacity = '1';
            this.locationIndicator.style.transform = 'translateY(0)';
        }, 10);
    }
    
    animate() {
        requestAnimationFrame(this.animate.bind(this));
        
        // Calculate delta time
        const delta = this.clock.getDelta();
        
        // Skip updates if still loading
        if (this.isLoading) {
            return;
        }
        
        // Update player controller
        this.playerController.update(delta);
        
        // Update investment objects
        const highlightedObject = this.investmentObjects.checkIntersection(this.camera);
        
        // Update visual effects
        this.visualEffects.update(delta, this.camera);
    }
}

// Initialize the application when the page loads
window.addEventListener('DOMContentLoaded', () => {
    const app = new MemoryMansionApp();
});
