import * as THREE from 'three';

// Class to handle dialogue system for investment explanations
export class DialogueSystem {
    constructor() {
        // DOM elements
        this.dialogueBox = document.getElementById('dialogue-box');
        this.dialogueTitle = document.getElementById('dialogue-title');
        this.dialogueContent = document.getElementById('dialogue-content');
        this.closeButton = document.getElementById('close-dialogue');
        
        // State
        this.isVisible = false;
        this.currentObject = null;
        
        // Setup event listeners
        this.setupEventListeners();
        
        // Audio for dialogue interactions
        this.audioListener = new THREE.AudioListener();
        this.sounds = {
            open: new THREE.Audio(this.audioListener),
            close: new THREE.Audio(this.audioListener)
        };
    }
    
    setupEventListeners() {
        // Close dialogue when close button is clicked
        this.closeButton.addEventListener('click', () => {
            this.hideDialogue();
        });
        
        // Close dialogue when ESC key is pressed
        document.addEventListener('keydown', (event) => {
            if (event.code === 'Escape' && this.isVisible) {
                this.hideDialogue();
            }
        });
        
        // Close dialogue when clicking outside the dialogue box
        document.addEventListener('click', (event) => {
            if (this.isVisible && !this.dialogueBox.contains(event.target)) {
                this.hideDialogue();
            }
        });
    }
    
    loadSounds(audioLoader) {
        // Load sound effects
        audioLoader.load('sounds/dialogue_open.mp3', (buffer) => {
            this.sounds.open.setBuffer(buffer);
            this.sounds.open.setVolume(0.5);
        });
        
        audioLoader.load('sounds/dialogue_close.mp3', (buffer) => {
            this.sounds.close.setBuffer(buffer);
            this.sounds.close.setVolume(0.5);
        });
    }
    
    showDialogue(title, content, object = null) {
        // Set dialogue content
        this.dialogueTitle.textContent = title;
        this.dialogueContent.textContent = content;
        
        // Store reference to the object
        this.currentObject = object;
        
        // Show the dialogue box with animation
        this.dialogueBox.style.display = 'block';
        this.dialogueBox.style.opacity = '0';
        this.dialogueBox.style.transform = 'translateY(20px) translateX(-50%)';
        
        // Trigger animation
        setTimeout(() => {
            this.dialogueBox.style.opacity = '1';
            this.dialogueBox.style.transform = 'translateY(0) translateX(-50%)';
        }, 10);
        
        // Play sound effect
        if (this.sounds.open.buffer) {
            this.sounds.open.play();
        }
        
        this.isVisible = true;
    }
    
    hideDialogue() {
        // Hide with animation
        this.dialogueBox.style.opacity = '0';
        this.dialogueBox.style.transform = 'translateY(20px) translateX(-50%)';
        
        // Play sound effect
        if (this.sounds.close.buffer) {
            this.sounds.close.play();
        }
        
        // Actually hide after animation completes
        setTimeout(() => {
            this.dialogueBox.style.display = 'none';
            this.currentObject = null;
        }, 300);
        
        this.isVisible = false;
    }
    
    isDialogueVisible() {
        return this.isVisible;
    }
    
    getCurrentObject() {
        return this.currentObject;
    }
    
    getAudioListener() {
        return this.audioListener;
    }
    
    // Method to create a codex entry style dialogue
    showCodexEntry(title, content, category = 'Investment Vehicle') {
        // Create a more stylized version of the dialogue for codex entries
        const formattedContent = `
            <div class="codex-category">${category}</div>
            <div class="codex-content">${content}</div>
            <div class="codex-footer">
                <div class="codex-icon"><img src="assets/icons/codex_icon.png" alt="Codex Icon"></div>
                <div class="codex-tip">Press ESC to close or E to add to your personal codex</div>
            </div>
        `;
        
        // Set dialogue content with HTML
        this.dialogueTitle.textContent = title;
        this.dialogueContent.innerHTML = formattedContent;
        
        // Show the dialogue with a different animation
        this.dialogueBox.style.display = 'block';
        this.dialogueBox.style.opacity = '0';
        this.dialogueBox.style.transform = 'scale(0.9) translateX(-50%)';
        
        // Add the codex class for special styling
        this.dialogueBox.classList.add('codex-style');
        
        // Trigger animation
        setTimeout(() => {
            this.dialogueBox.style.opacity = '1';
            this.dialogueBox.style.transform = 'scale(1) translateX(-50%)';
        }, 10);
        
        // Play a special codex sound
        if (this.sounds.open.buffer) {
            this.sounds.open.play();
        }
        
        this.isVisible = true;
    }
    
    // Method to show a tutorial dialogue
    showTutorial(title, steps) {
        // Format the steps as HTML
        let stepsHtml = '<ol class="tutorial-steps">';
        steps.forEach(step => {
            stepsHtml += `<li>${step}</li>`;
        });
        stepsHtml += '</ol>';
        
        // Create the tutorial content
        const tutorialContent = `
            <div class="tutorial-content">
                ${stepsHtml}
            </div>
            <div class="tutorial-footer">
                <div class="tutorial-icon"><img src="assets/icons/tutorial_icon.png" alt="Tutorial Icon"></div>
                <div class="tutorial-tip">Press ESC to close or SPACE to continue</div>
            </div>
        `;
        
        // Set dialogue content with HTML
        this.dialogueTitle.textContent = title;
        this.dialogueContent.innerHTML = tutorialContent;
        
        // Show the dialogue with a different animation
        this.dialogueBox.style.display = 'block';
        this.dialogueBox.style.opacity = '0';
        this.dialogueBox.style.transform = 'translateY(-20px) translateX(-50%)';
        
        // Add the tutorial class for special styling
        this.dialogueBox.classList.add('tutorial-style');
        
        // Trigger animation
        setTimeout(() => {
            this.dialogueBox.style.opacity = '1';
            this.dialogueBox.style.transform = 'translateY(0) translateX(-50%)';
        }, 10);
        
        // Play a tutorial sound
        if (this.sounds.open.buffer) {
            this.sounds.open.play();
        }
        
        this.isVisible = true;
    }
    
    // Method to show an investment comparison dialogue
    showInvestmentComparison(title, investments) {
        // Create a comparison table
        let tableHtml = '<table class="comparison-table"><thead><tr><th>Feature</th>';
        
        // Add investment names as column headers
        investments.forEach(inv => {
            tableHtml += `<th>${inv.name}</th>`;
        });
        tableHtml += '</tr></thead><tbody>';
        
        // Add rows for each feature
        const features = ['Risk Level', 'Liquidity', 'Tax Treatment', 'Typical Return', 'Minimum Investment'];
        
        features.forEach(feature => {
            tableHtml += `<tr><td>${feature}</td>`;
            
            investments.forEach(inv => {
                tableHtml += `<td>${inv[feature.toLowerCase().replace(' ', '_')]}</td>`;
            });
            
            tableHtml += '</tr>';
        });
        
        tableHtml += '</tbody></table>';
        
        // Set dialogue content with HTML
        this.dialogueTitle.textContent = title;
        this.dialogueContent.innerHTML = tableHtml;
        
        // Show the dialogue with a different animation
        this.dialogueBox.style.display = 'block';
        this.dialogueBox.style.width = '90%'; // Wider for comparison tables
        this.dialogueBox.style.opacity = '0';
        this.dialogueBox.style.transform = 'translateY(0) translateX(-50%) scale(0.95)';
        
        // Add the comparison class for special styling
        this.dialogueBox.classList.add('comparison-style');
        
        // Trigger animation
        setTimeout(() => {
            this.dialogueBox.style.opacity = '1';
            this.dialogueBox.style.transform = 'translateY(0) translateX(-50%) scale(1)';
        }, 10);
        
        // Play sound
        if (this.sounds.open.buffer) {
            this.sounds.open.play();
        }
        
        this.isVisible = true;
    }
    
    // Reset all special styles when closing
    resetStyles() {
        this.dialogueBox.classList.remove('codex-style', 'tutorial-style', 'comparison-style');
        this.dialogueBox.style.width = '80%'; // Reset to default width
        this.dialogueContent.innerHTML = ''; // Clear any HTML content
    }
    
    // Override the hide method to also reset styles
    hideDialogue() {
        // Hide with animation
        this.dialogueBox.style.opacity = '0';
        this.dialogueBox.style.transform = 'translateY(20px) translateX(-50%)';
        
        // Play sound effect
        if (this.sounds.close.buffer) {
            this.sounds.close.play();
        }
        
        // Actually hide after animation completes
        setTimeout(() => {
            this.dialogueBox.style.display = 'none';
            this.currentObject = null;
            this.resetStyles(); // Reset any special styles
        }, 300);
        
        this.isVisible = false;
    }
}
